main {
    padding: 36px;
}

.header {
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    padding: 10px;
}

.header--image {
    padding-right: 6px;
}

.header--title {
    margin-right: auto;
}

.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr ;
    gap: 17px;
}

.form--btn {
    grid-column: 1 / -1;
    border-radius: 5px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    border: none;
    cursor: pointer;
}

.form--input{
    border-radius: 3px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;
}

.memeImg {
    align-items: center;
    max-width: 500px;
    max-height: 300px;
    padding: 50px 15px 15px 15px;
    border-radius: 2px;
}


.meme {
    position: relative;
}

.meme--image {
    max-width: 100%;
    border-radius: 3px;
    padding-top: 15px;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}












































